<template>
  <!-- navbar -->
  <div class="navbar">
  <router-link class="link" to="/">
  <p class="item-menu">
     Calculator
  </p>
  </router-link>
    <router-link class="link" to="/ProductComp">
      <p class="item-menu">
          ProductComp
      </p>
    </router-link>
  </div>
  <!-- h1 -->
  <h1 class="header">Calculator</h1>
   <!-- Calculator -->
  <div class="output">
    <div class="outputCalc">
      <!-- {{ calculatorValue || 0}} -->
      {{ calculatorValue || operator || 0}}
    </div>
  </div>
  <div class="btn">
    <div
      class="button"
      v-for="(n, index) in btnArr"
      :key="index"
      :class="{ operator: ['C', '+', '-', '*', '/', '%', '='].includes(n.number), active: n.is_active }"
      >
      
    <div class="btn" @click="action(n.number, index)">
        {{ n.number }}
      </div>
    </div>
  </div>

</template>

<script>
export default {
  name: 'CalculatorComp',
  data() {
    return {
      isActive: false,
      decimalAdded: false,
      display: '',
      prevOps: 0,
      calculatorValue: "",
      n: '',
      isResult: false,
      btnArr: [
        {number: "7", is_active: false},
        {number: "8", is_active: false},
        {number: "9", is_active: false},
        {number: "+", is_active: false},
        {number: "4", is_active: false},
        {number: "5", is_active: false},
        {number: "6", is_active: false},
        {number: "-", is_active: false},
        {number: "1", is_active: false},
        {number: "2", is_active: false},
        {number: "3", is_active: false},
        {number: "*", is_active: false},
        {number: "0", is_active: false},
        {number: ".", is_active: false},
        {number: "%", is_active: false},
        {number: "/", is_active: false},
        {number: "=", is_active: false},
        {number: "C", is_active: false},
      ], 
      previousCalculatorValue: "",
      operator: ''
    };
}, 
  methods: {
    action(n, index) {
      // console.log(index)
      this.btnArr.some(e => e.is_active = false)
      this.btnArr[index].is_active = true
      // action "append" with "."
      if (!isNaN(n) || n === ".") {
        if (this.isResult) {
          this.calculatorValue = n;
          this.isResult = false;
        } else this.calculatorValue += n + "";
      }
      // add decimal
      if (this.decimalAdded == false) {
        if (this.prevOps != 0) {
          this.display = '0.';
        } else {
          this.display += '.';
        }
        this.decimalAdded = true;
      }
      // action "clear" with "C"
      if (n === "C") {
        this.calculatorValue = "";
      }
      // action "percent" with "%"
      if (n === "%"){
        this.calculatorValue = this.calculatorValue /100 + "";
      }
      //action "operate" with "+, -, *, /"
      if (["+", "-", "*", "/"].includes(n)) {
        this.operator = n;
        this.previousCalculatorValue = this.calculatorValue;
        this.calculatorValue = "";
      }
      // action "calculate" with "="
      if (n === "=") {
        this.calculatorValue = eval(
        this.previousCalculatorValue + this.operator + this.calculatorValue);
        this.previousCalculatorValue = "";
        this.operator = null;
        this.isResult = true;
      }
      console.log(this.calculatorValue)
    },
  },
};
</script>

